import 'isomorphic-fetch';
import { Component } from 'react';

export default class extends Component {

    static async getInitialProps({ query }) {
        const idAudio = query.id;
        console.log(query.id);
        
        const requestClip = await fetch (`https://api.audioboom.com/audio_clips/${ idAudio }.mp3`);
        
        const { body : { audio_clip } } = await requestClip.json();
        console.log(audio_clip.title);
        return { audio_clip };
    }
    render(){

        const { audio_clip } = this.props
        return (
                <div>
                    <div className="cover">
                        <img src={ audio_clip.urls.post_image.original } alt="cover"/>
                        <h2>{ audio_clip.title }</h2>
                    </div>
                    <div>
                        {/* <audio src={ audio_clip.urls.high_mp3 } controls/> */}
                    </div>
                    <style jsx>{`
                        .channels {
                            display: grid;
                            grid-gap: 15px;
                            padding: 15px;
                            grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
                        }
                        cover {
                            background:  #55E6C1;
                        }
                        .cover img {
                            border-radius: 3px;
                            box-shadow: 0px 2px 6px rgba(0,0,0,0.15);
                            width: 20%;
                            height: 20%;
                            align: center;
                        }
                        h1 {
                            padding: 15px;
                            font-weight: 600;
                        }
                        h2 {
                            padding: 5px;
                            font-size: 0.9em;
                            font-weight: 600;
                            margin: 0;
                            text-align: absolute;
                        }
                    `}</style>
                    <style jsx global>{`
                        body {
                            margin: 0;
                            background: white;
                            font-family: system-ui;
                        }
                    `}</style>
                </div>
        )
    }
}



